
#nav {
  padding-top: 10px;
  background-color: var(--primary-normal);
  color: var(--primary-lightest);
  height: 100px;
  outline: 1px solid var(--primary-lightest);
}

#nav a {
  color: var(--primary-white);
  text-decoration: none;
}

#nav-container {
  align-items: center;
  height: 100%;
}

#nav-logo-container {
  width: 85px;
  height: 85px;
  background-color: var(--primary-lightest);
  color: var(--primary-dark);
  border-radius: 11px;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: 1px solid var(--primary-dark);
}

#nav-info-container {
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  margin-bottom: 5px;
  margin-left: 20px;
}

#nav-title {
  align-items: center;
}

#nav-title h6 {
  font-size: 0.8rem;
  margin-top: 3px;
}

#nav-links-container {
  padding-bottom: 1px;
}

#nav-links {
  margin-left: 3px;
  align-items: center;
}

#nav-links li a div {
  cursor: pointer;
  margin-right: 40px;
  background-color: var(--primary-lightest);
  color: var(--primary-dark);
  padding: 7px 13px;
  min-width: 50px;
  text-align: center;
  border-radius: 11px;
  font-size: 0.8rem;
}

#nav-links li a div:hover {
  outline: 1px solid var(--primary-secondary);
  color: var(--primary-secondary);
}

.nav-highlight div {
  outline: 2px solid var(--primary-secondary);
  font-weight: bold;
  color: var(--primary-secondary) !important;
}

.nav-highlight div:hover {
  outline: 2px solid var(--primary-secondary) !important;
}