:root {
  --primary-dark: #1a222e;
  --primary-normal: #506d94;
  --primary-light: #cddce6;
  --primary-lightest: #f4ffff;
  --primary-secondary: #d46144;
}

body,
html {
    font-family: 'Roboto', sans-serif;
}

body {
  height: 100%;
  min-height: 100vh;
  position: relative;
  background-color: var(--primary-light);
}

.flex {
  display: flex;
}

.container {
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
}

h1, h2, h3, h4 {
  font-weight: bold;
}

h3 {
  font-size: 2rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.15rem;
}

.padding-bottom-20 {
  padding-bottom: 20px;
}

.content-container {
  outline: 1px solid var(--primary-dark);
  border-radius: 11px;
  background-color: var(--primary-lightest);
  line-height: 1.4rem;
  margin-bottom: 50px;
}

.content {
  width: 85%;
  margin: 0 auto;
  padding: 30px 0;
}

/*      HOME      */

#home {
  margin-top: 50px;
  color: var(--primary-dark);
}

#home-intro a {
  color: var(--primary-normal);
  text-decoration: underline;
}

#home-categories-list {
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin-left: 10%;
}

#home-categories-list li {
  width: 30%;
  margin: 20px 0;
}

#home-categories-list-last {
  margin-bottom: none;
}

#home-categories-list li a {
  color: var(--primary-dark);
  text-decoration: none;
  font-size: 1.05rem;
}

#home-categories-list li a:hover {
  text-decoration: underline;
}

/*      INFOMAIN      */

#info-main {
  margin-top: 50px;
}

#info-main-name-container {
  justify-content: space-between;
  width: 80%;
  margin: 0 auto;
  padding-bottom: 40px;
  color: var(--primary-dark);
}

.info-main-type,
.info-main-type-placeholder {
  min-width: 60px;
  text-align: center;
  margin: 0 10px;
}

.info-main-type {
  border: 1px solid var(--primary-dark);
  border-radius: 7px;
  font-size: 0.9rem;
  color: var(--primary-lightest);
  background-color: var(--primary-normal);
}

.info-main-type-placeholder {
  border: 1px solid var(--primary-lightest);
}

#info-main-stats-container {
  justify-content: space-between;
  width: 96%;
  color: var(--primary-dark);
  align-items: center;
}

#info-main-img {
  height: 100%;
  width: 38%;
  display: flex;
  justify-content: center;
  margin-bottom: 7px;
}

#info-main-img img {
  min-width: 180px;
  border: 1px solid var(--primary-normal);
  border-radius: 50%;
  background-color: var(--primary-light);
}

#info-main-stats {
  line-height: 2rem;
  font-size: 1rem;
  width: 45%;
  padding: 15px 40px;
  border: 1px solid var(--primary-normal);
  border-radius: 8px;
}

.info-main-stat-name {
  width: 70px;
  margin-right: 10px;
  text-align: right;
}

.info-main-stat-number {
  width: 30px;
  text-align: right;
}

#info-evolution {
  justify-content: center;
  margin-top: 30px;
}

.evo-chain-container {
  display: flex;
  flex-direction: column;
}

.info-main-evo-img {
  margin: 0 20px;
}

.info-main-evo-img img {
  width: 100%;
}

/*      INFO SECONDARY      */

.info-secondary-container {
  width: 80%;
  margin: 0 auto;
}

.info-secondary-first-box {
  width: 50%;
}

.info-egg-type {
  min-width: 60px;
  text-align: center;
  margin: 0 8px;
  border: 1px solid var(--primary-dark);
  border-radius: 7px;
  font-size: 0.9rem;
  color: var(--primary-lightest);
  background-color: var(--primary-normal);
  padding: 0 2px;
}

/*      INFO MOVES     */

#info-moves-generations-container {
  justify-content: space-evenly;
  width: 80%;
  margin: 0 auto;
}

.info-gen-button {
  min-width: 75px;
  border: 1px solid var(--primary-dark);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  padding: 4px 3vw;
  font-size: 0.7rem;
}

#info-moves-container {
  width: 80%;
  margin: 30px auto 0;
  padding-top: 50px;
  border-top: 1px solid var(--primary-dark);
}

.info-moves-display-list {
  display: flex;
  flex-wrap: wrap;
}

.info-moves-display-move {
  width: 47%;
  outline: 1px solid var(--primary-dark);
  text-align: center;
  margin: 0 1.5% 5px;
  outline-style: dashed;
  border-radius: 5px;
}

.info-moves-display-level {
  width: 20%;
  border-right: 1px solid var(--primary-dark);
  border-style: dashed;
}

.info-moves-display-level-title {
  width: 80%;
}

.info-moves-display-gen-title {
  margin-bottom: 20px;
}

.info-moves-display-move-container {
  margin-bottom: 25px;
}

.info-moves-display-move-container h5 {
  margin-bottom: 5px;
  font-style: italic;
  font-size: 1.05rem;
}